.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-footer {
  background-color: #222;
  font-size: large;
  height: 80px;
  padding: 20px;
  color: white;
}
.container {
  background-color: #f2f2f2;
  border-radius: 7px;
}

.languageSelection p {
  text-align: right;
  margin-right: 50px;
  margin-top: 5px;
  padding-top: 5px;
}

.languageSelection p a {
  padding-left: 10px;
  padding-right: 10px;
  font-size: large;

}

.languageSelection p a:hover {
  background-color: lightgray;
  border-radius: 10;
  text-decoration: underline;
}

.homestyle {
  background-color: #222;
  padding: 20px;
  color: white;
  border-radius: 4px;
}

.homestyle h2 {
  padding-bottom:5px;
}

.home-upperBlock {
  padding-top: 25px;
  font-size: large;
}

.home-lowerBlock {
  padding-top: 15px;
  padding-bottom: 25px;
}

.homestyle img {
  width: 100px;
  margin-top: 5px;
  margin-right:5px;
  border-radius: 2px;
}

.startBlock {
  margin-top: 20px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
}

.startBlock p {
  padding-top: 2px;
  padding-bottom: 2px;
  margin: auto;
  width: 90px;
  background-color: lightgray;
  border-radius: 4px;
}

.blackButton {
  color: #222;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 4px;
  padding-bottom: 4px;
}

.blackButton a {
  padding-left: 12px;
  padding-right: 12px;
  padding-top: 1.5px;
  padding-bottom: 1.5px;
}

.blackButton a:hover {
  border-radius: 4px;
  background-color: lightgray;
}

.blueButton {
  color: #0080ff;
}

.resultbox {
  padding-top: 20px;
  padding-bottom: 10px;
  fontsize: large;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



